	module.exports = {"data":{"post":{"html":"<p><a title=\"Instapundit.com:\" href=\"http://www.instapundit.com/archives/010213.php#010213\">Instapundit</a> points to a Matt Welch article on <a title=\"NATIONAL POST\" href=\"http://www.nationalpost.com/review/story.html?id=BEE0DE33-3215-4E9F-933B-C1951689F7FC\">Turning baseball on its ear</a>, how the the unconventional Bill James method of looking at statistics (bases, runs and wins instead of hits) is making its mark on professional baseball. InstaPundit compares the 'Jamesian' movement to weblogging. In the '80s I was a religious reader of Bill James' Abstracts, and participated in Project Scorecard (mentioned in the article). I still remember that it takes 5 runs to win a game. And I hear Bill James now   <a title=\"USATODAY.com - Red Sox hire 'Baseball Abstract' author Bill James\" href=\"http://www.usatoday.com/sports/baseball/al/redsox/2002-11-15-james_x.htm\">works for the Red Sox</a>.</p>","fields":{"title":"Baseball on its ear","date":"22 Jun 2003","url":"https://papascott.de/2003-06-22-baseball-on-its-ear/"}}},"pathContext":{"slug":"/2003-06-22-baseball-on-its-ear/","prev":{"fields":{"title":"Saddam's Homepage Gets Face Lift","date":"21 Jun 2003","slug":"/2003-06-21-saddams-homepage-gets-face-lift/"}},"next":{"fields":{"title":"Baghdad Bulletin - Independent news from Iraq","date":"23 Jun 2003","slug":"/2003-06-23-baghdad-bulletin-independent-news-from-iraq/"}}}}

